import {
  IonButton,
  IonCol,
  IonContent,
  IonFabButton,
  IonIcon,
  IonItem,
  IonLabel,
  IonList,
  IonModal,
} from "@ionic/react";
import { useDispatch, useSelector } from "react-redux";

import { history } from "../../_helpers/history";
import { partidaActions } from "../../_redux/actions";
import { useEffect, useState } from "react";
import { GameState } from "../../_redux/models/game.model";
import { useTranslation } from "react-i18next";
import {
  person,
  notifications,
  chevronForward,
  gameController,
  earth,
  apps,
  locate,
  image,
  play,
  globe,
  storefrontSharp,
  closeOutline,
  contract,
  contrastOutline,
  settings,
} from "ionicons/icons";
import { PartidaState } from "../../_redux/models/partida.model";
import { SocialRed } from "../Social/SocialRed";
import { appActions } from "../../_redux/actions/app.actions";
import AdsGoogleAdsense from "../Ads/AdsGoogleAdsense";
import Ads from "../Ads/GoogleAd";
import { IonRow } from "@ionic/react";
import BannerLocal240 from "../Ads/BannerLocal240";
import Media from 'react-media';

export const MenuLateral: React.FC<any> = () => {
  const dispatch = useDispatch();
  const { t } = useTranslation();

  const partida: PartidaState = useSelector((state: any) => state.partida);
  const game: GameState = useSelector((state: any) => state.game);
  const [showModal, setShowModal] = useState(false);
  const [urlSocial, setUrlSocial] = useState<string>(
    "https://twitter.com/PekeGames"
  );
  const handlePlay = () => {
    dispatch(
      partidaActions.proximaPregunta(
        partida.answered,
        partida.currentQuestion,
        game.cities,
        game.provinceCities
      )
    );
    partida.gameStart = true;
    history.push("/quiz");
  };

  const modoOscuro = () => {
    try {
      if (localStorage.getItem("appTheme") === "") {
        dispatch(appActions.setAppTheme("dark"));
      } else {
        dispatch(appActions.setAppTheme(""));
      }
    } catch (error) {
      dispatch(appActions.setAppTheme(""));
    }
  };
  const handleRanking = () => {
    history.push("/ranking");
  };

  const handleProfile = () => {
    history.push("/profile");
  };

  const handleCity = () => {
    history.push("/cities");
  };

  const handlePhoto = () => {
    history.push("/photo");
  };

  const handleApps = () => {
    history.push("/apps");
  };

  useEffect(() => {
    dispatch(partidaActions.recuperarPartida());
  }, []);

  return (
    <div className="c_menu_lateral">
      <div className="social_barra">
        <IonFabButton size="small" color="light">
          <IonIcon icon={notifications} />
        </IonFabButton>
        <IonFabButton size="small" color="light">
          <IonIcon icon={person} />
        </IonFabButton>
      </div>

      <IonFabButton
        onClick={handleProfile}
        className="icon_settings hide_pc_global margin_icon"
        color="light"
      >
        <IonIcon color="medium" size="large" icon={settings} />
      </IonFabButton>
      <IonFabButton
        onClick={modoOscuro}
        className="icon_settings hide_pc_global margin_icon"
        color="dark"
      >
        <IonIcon size="large" icon={contrastOutline} />
      </IonFabButton>
      <IonFabButton
        routerLink="store"
        className="icon_settings hide_pc_global margin_icon"
        color="warning"
      >
        <IonIcon color="danger" size="large" icon={storefrontSharp} />
      </IonFabButton>

      <div className="login_buttons_group_home">
        <IonRow>
          <IonCol className="text_center titulo_boton_home">
            <img
              onClick={handleCity}
              src="/assets/icon/ciudad.png"
              alt="Ciudad"
              className="icon_home_bt"
            ></img>
            <h3> {t("cities")}</h3>
          </IonCol>
          <IonCol  className="text_center titulo_boton_home">
            <img
              onClick={handleRanking}
              src="/assets/icon/ranking.png"
              alt="Ranking"
              className="icon_home_bt"
            ></img>
             <h3> {t("ranking")}</h3>
          </IonCol>
          <IonCol  className="text_center titulo_boton_home">
            <img
              onClick={handlePhoto}
              src="/assets/icon/turismo.png"
              alt="Ranking"
              className="icon_home_bt"
            ></img>
             <h3> {t("tourism")}</h3>
          </IonCol>
        </IonRow>
        <IonButton
          shape="round"
          size="large"
          className="btn_sty bt_play_margin"
          onClick={handlePlay}
        >
          <IonIcon slot="start" icon={play} />
          {partida.gameStart && partida.currentQuestion > 1
            ? t("continue_game")
            : t("play")}
        </IonButton>
        {/*    <IonButton
          shape="round"
          size="large"
          color="tertiary"
          className="btn_sty"
          onClick={handleRanking}
        >
          <IonIcon slot="start" icon={globe} />
          {t("ranking")}
        </IonButton>

        <IonButton
          shape="round"
          size="large"
          color="warning"
          className="btn_sty"
          onClick={handlePhoto}
        >
          <IonIcon slot="start" icon={image} />
          {t("tourist_points")}
        </IonButton>

        <IonButton
          shape="round"
          size="small"
          color="success"
          className="btn_sty"
          onClick={handleCity}
        >
          <IonIcon slot="start" icon={locate} />
          {t("cities")}
        </IonButton>
 */}
      </div>

      <div className="menu_lateral_pc">
        <IonList>
          <IonItem onClick={handlePlay}>
            <IonIcon color="primary" icon={gameController} />
            <IonLabel className="btn_menu_home" color="primary">
              {" "}
              {partida.currentQuestion>1 ? t("continue_game") : t("play")}
            </IonLabel>
            <IonIcon icon={chevronForward} />
          </IonItem>

          <IonItem onClick={handleRanking}>
            <IonIcon color="primary" icon={earth} />
            <IonLabel className="btn_menu_home"> {t("ranking")}</IonLabel>
            <IonIcon icon={chevronForward} />
          </IonItem>

          <IonItem onClick={handleCity}>
            <IonIcon color="primary" icon={locate} />
            <IonLabel className="btn_menu_home"> {t("cities")}</IonLabel>
            <IonIcon icon={chevronForward} />
          </IonItem>

          <IonItem onClick={handlePhoto}>
            <IonIcon color="primary" icon={image} />
            <IonLabel className="btn_menu_home">
              {" "}
              {t("tourist_points")}
            </IonLabel>
            <IonIcon icon={chevronForward} />
          </IonItem>

          <IonItem onClick={handleApps}>
            <IonIcon color="primary" icon={apps} />
            <IonLabel className="btn_menu_home"> {t("other_apps")}</IonLabel>
            <IonIcon icon={chevronForward} />
          </IonItem>

          {/*
          <IonItem onClick={handleProfile}>
            <IonIcon icon={person} />
            <IonLabel> {t("profile")}</IonLabel>
            <IonIcon icon={chevronForward} />
          </IonItem>

          <IonItem>
            <IonIcon icon={notifications} />
            <IonLabel> {t("notifications")}</IonLabel>
            <IonIcon icon={chevronForward} />
          </IonItem>

         

          <IonItem>
            <IonIcon icon={share} />
            <IonLabel> {t("share")}</IonLabel>
            <IonIcon icon={chevronForward} />
          </IonItem>
*/}
        </IonList>

        <SocialRed></SocialRed>

     
        <Media query="(min-width: 600px)" render={() =>
          (
            <BannerLocal240></BannerLocal240>
          )}
        />
      </div>
      <IonModal isOpen={showModal} cssClass="fullscreen">
        <IonContent>
          <iframe
            id="mymodal"
            src={partida.response?.W}
            className="iframeStyle"
          ></iframe>
        </IonContent>
        <IonIcon
          className="modal_close"
          onClick={() => setShowModal(false)}
          icon={closeOutline}
        />
      </IonModal>
    </div>
  );
};

export default MenuLateral;
