import {
  IonBackButton,
  IonButton,
  IonButtons,
  IonCard,
  IonCardContent,
  IonCardTitle,
  IonCol,
  IonContent,
  IonFooter,
  IonGrid,
  IonHeader,
  IonIcon,
  IonInput,
  IonItem,
  IonLabel,
  IonList,
  IonModal,
  IonPage,
  IonRow,
  IonText,
  IonTitle,
  IonToolbar,
  useIonActionSheet,
  useIonViewDidEnter,
} from "@ionic/react";
import { useSelector } from "react-redux";
import { AuthState } from "../../_redux/models/auth.model";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { authActions, gameActions, logroActions } from "../../_redux/actions";
import styles from "./profile.module.scss";
import { useEffect, useState } from "react";
import {
  apps,
  chevronForward,
  closeOutline,
  create,
  ellipsisHorizontal,
  ellipsisVertical,
  fileTrayFull,
  gameController,
  images,
  language,
  logoFacebook,
  logoInstagram,
  logoTwitter,
  logoWebComponent,
  logoYoutube,
  map,
  person,
  personAdd,
  settings,
  trash,
} from "ionicons/icons";
import { history } from "../../_helpers/history";
import { gameService } from "../../_redux/services";
import ListAvatar from "../../components/Avatar/ListAvatar";
import CrearCuenta from "../../components/Modal/CrearCuenta";
import { MenuApp, getVersion } from "../../utils/functions";
import i18n from "../../i18nextConf";
import ListadoLogro from "../../components/Logro/ListadoLogro";
import VerLogro from "../../components/Logro/VerLogro";
import { LogroState, LogrosUsuario } from "../../_redux/models/logro.models";
import listadoLogros from "../../data/logro.json";
import GanaLogro from "../../components/Logro/GanaLogro";
import { appActions } from "../../_redux/actions/app.actions";

const Profile: React.FC = () => {
  const { t } = useTranslation();
  const authUser: AuthState = useSelector((state: any) => state.auth);
  const logros: LogroState = useSelector((state: any) => state.logro);
  const [present, dismiss] = useIonActionSheet();
  const [loginModal, setLoginModal] = useState(false);
  const [logroModal, setLogroModal] = useState(false);
  const [logroGanaModal, setLogroGanaModal] = useState(false);

  const [verLogroModal, setVerLogroModal] = useState(false);
  const [logroSelect, setLogroSelect] = useState<LogrosUsuario>();

  const abrirModalLogro = () => {
    setLogroModal(true);
  };

  const cerrarModalLogro = () => {
    setLogroModal(false);
  };
  const abrirModal = () => {
    setLoginModal(true);
  };

  const abrirModalVerLogro = (logro: LogrosUsuario) => {
    setLogroSelect(logro);
    setVerLogroModal(true);
  };

  const cerrarModalVerLogro = () => {
    setVerLogroModal(false);
  };

  const cerrarModal = () => {
    setLoginModal(false);
  };
  const dispatch = useDispatch();
  const [version, setVersion] = useState("");

  const [shownickModal, setShownickModal] = useState(false);
  const [nick, setNick] = useState<string>("Nick");

  const [showAvatarModal, setShowAvatarModal] = useState(false);
  const [showIdiomaModal, setShowIdiomaModal] = useState(false);
  const [showPrivacidadModal, setShowPrivacidadModal] = useState(false);
  const [showCuentaModal, setShowCuentaModal] = useState(false);
  const [showAppModal, setShowAppModal] = useState(false);

  const [avatar, setAvatar] = useState<string>("");
  const final = [];

  const modificarIdioma = (language: string) => {
    i18n.changeLanguage(language);
    setShowIdiomaModal(false);
  };

  const facebookLink = () => {
    if (!logros?.logro?.find((x) => x.LogroId === 29)?.TieneLogro) {
       dispatch(appActions.showLogro(29));
    }

    window.open(
      "https://www.facebook.com/people/Peke-Games/100037288136176/",
      "_blank"
    );
  };
  const twitterLink = () => {
    if (!logros?.logro?.find((x) => x.LogroId === 29)?.TieneLogro) {
      dispatch(appActions.showLogro(29));
   }
    window.open("https://twitter.com/PekeGames", "_blank");
  };
  const pekegamesLink = () => {
    window.open("https://www.pekegames.app", "_blank");
  };
  const youtubeLink = () => {
    if (!logros?.logro?.find((x) => x.LogroId === 29)?.TieneLogro) {
      dispatch(appActions.showLogro(29));
   }
    window.open(
      "https://www.youtube.com/channel/UCGUe2P_-tURFmAtEHvpUGoQ",
      "_blank"
    );
  };
  const instagramLink = () => {
    if (!logros?.logro?.find((x) => x.LogroId === 29)?.TieneLogro) {
      dispatch(appActions.showLogro(29));
   }
    window.open("https://www.instagram.com/pekegamess/", "_blank");
  };

  const modificarAvatar = (myavatar: number) => {
    try {
      setAvatar("a" + myavatar + ".svg");
      authUser.user.Avatar = "a" + myavatar + ".svg";
      gameService.CambiarAvatar(authUser.user);
      setShowAvatarModal(false);
    } catch (error) {
      // alert(error);
    }
  };

  for (let i = 1; i <= 165; i++) {
    final.push(
      <img
        alt="avatar"
        src={`https://www.pekegames.app/img/avatar/a${i}.svg`}
        onClick={() => modificarAvatar(i)}
        className="img_avatar_mini"
        key={i}
      ></img>
    );
  }

  useEffect(() => {
    listadoLogros.map((logro) => {
      logro.Description = t(logro.Description);
      logro.DescriptionGanoLogro = t(logro.DescriptionGanoLogro);
      logro.Name = t(logro.Name);
    });

    {
      authUser?.isAuth &&
        Array.isArray(authUser.user?.LogrosUsuario) &&
        authUser.user?.LogrosUsuario.map((logroItem) => {
          listadoLogros.map((logro) => {
            if (logroItem.CodigoLogro === logro.LogroId) {
              logro.TieneLogro = true;
            }
          });
        });
    }
    dispatch(logroActions.setLogro(listadoLogros));

    if (authUser?.user?.NuevoUsuarioEnElJuego) {
      dispatch(appActions.showLogro(1));
    }
  }, [authUser, dispatch, t]);

  useEffect(() => {
    console.log("useEffect", authUser.user);

    async function fetchVersion() {
      dispatch(gameActions.IsBusy());
      setVersion(await getVersion());

      if (authUser?.isAuth && authUser?.user?.Id !== "-1") {
        const perfil = await gameService.Perfil();
        if (perfil !== null && perfil.Id!=="-1")  {
          console.log("PERFILV",perfil);
          dispatch(authActions.cargarDatosSinNavegacion(perfil));
        }
      } else {
        console.log("no llamo a nada");
      }
      dispatch(gameActions.QuitarIsBusy());
    }
    fetchVersion();
  }, []);

  /*
  useEffect(() => {
    setNick(authUser?.user?.Nick);
  }, [authUser]);
*/
  const modalEliminarCuenta = () => {
    setShowCuentaModal(true);
  };

  const cerrarModaGanalLogro = () => {
    setLogroGanaModal(false);
  };

  const modalSobreLaApp = () => {
    history.push("about");
    //setShowAppModal(true);
  };
  const deleteCuentaPeke = async () => {
    try {
      await gameService.EliminarCuentaDeFormaDefinitiva(authUser?.user);
      dispatch(authActions.logout());
      history.push("/login");
    } catch (error) {
      alert("error");
    }
  };
  const modalNick = () => {
    setShownickModal(true);
  };
  const modalAvatar = () => {
    setShowAvatarModal(true);
  };

  const modalIdioma = () => {
    setShowIdiomaModal(true);
  };

  const modalPrivacidad = () => {
    setShowPrivacidadModal(true);
  };

  const tienda = () => {
    history.push("store");
  };

  const modificarNick = () => {
    try {
      if (nick !== null && nick.length >= 3) {
        authUser.user.Nick = nick;
        gameService.CambiarNick(authUser.user);
        setShownickModal(false);
      }
    } catch (error) {
      alert(error);
    }
  };

  const logout = () => {
    dispatch(authActions.logout());
  };

  return (
    <IonPage id="page-dashboard" className="page-dashboard">
      <IonHeader>
        <IonToolbar>
          {MenuApp(present, t)}
          <IonTitle>{t("settings")}</IonTitle>
          <IonButtons slot="start">
            <IonBackButton defaultHref="dashboard" />
          </IonButtons>
        </IonToolbar>
      </IonHeader>
      <IonContent className="bg_app_content ">
        <IonRow className="contenido_scroll">
          <IonCol size="12" size-md="8" className={styles.centro_pagina}>
            <div className={styles.headerPhotoProfile}>
              {authUser?.isAuth && (
                <img
                  className={styles.avatar1}
                  alt="Avatar"
                  src={`https://www.pekegames.app/img/avatar/${authUser.user?.Avatar}`}
                ></img>
              )}

              {!authUser?.isAuth && (
                <img
                  className={styles.avatar1}
                  alt="Avatar"
                  src={`https://www.pekegames.app/img/avatar/a0.svg`}
                ></img>
              )}
            </div>

            <div className={styles.headerDataProfile}>
              <span>
                {!authUser?.isAuth ? t("guest") : authUser.user?.Nick}
              </span>
            </div>

            {authUser?.isAuth && (
              <div className={styles.caja_profile}>
                <h3 className="tituonClick_principal">
                  {t("balance_in_pekeGames")}
                </h3>
                <IonRow>
                  <IonCol className="text_center"></IonCol>
                  <IonCol className="text_center"></IonCol>
                </IonRow>
                <IonRow>
                  <IonCol className="text_center ">
                    <img
                      width={60}
                      src="/assets/icon/moneda.png"
                      alt="moneda"
                    ></img>
                    <span>{authUser.user?.DatosExtendido?.MonedaGlobal}</span>
                  </IonCol>
                  <IonCol className="text_center">
                    <img
                      width={60}
                      src="/assets/icon/puntos.png"
                      alt={t("points")}
                    ></img>
                    <span>
                      {" "}
                      {authUser.user?.DatosExtendido?.PuntosGlobales}
                    </span>
                  </IonCol>
                </IonRow>
                <IonButton color="success" shape="round" onClick={tienda}>
                  {t("buy_with_my_coins")}
                </IonButton>
              </div>
            )}

            {!authUser?.isAuth && (
              <div className={styles.caja_profile}>
                <h3>{t("create_account")}</h3>
                <p>{t("create_account_description")}</p>
                <IonButton color="success" shape="round" onClick={abrirModal}>
                  <IonIcon slot="start" icon={personAdd} />
                  {t("create_account_button")}
                </IonButton>
              </div>
            )}

            {authUser?.isAuth &&
              Array.isArray(authUser.user?.LogrosUsuario) && (
                <div className={styles.caja_profile}>
                  <h3 className="titulo_principal">Insignias</h3>
                  <hr></hr>
                  <div className="horizontal-scroll-wrapper squares">
                    {Array.isArray(authUser.user?.LogrosUsuario) &&
                      authUser.user?.LogrosUsuario.slice(0, 3).map(
                        (item, index) => {
                          return (
                            <img
                              key={index}
                              onClick={() => abrirModalVerLogro(item)}
                              alt="logo"
                              className={styles.logo_img}
                              src={
                                "https://www.pekegames.app/img/logro/" +
                                item?.Image
                              }
                            ></img>
                          );
                        }
                      )}
                  </div>
                  <IonButton
                    color="success"
                    shape="round"
                    onClick={abrirModalLogro}
                  >
                    {t("see_all_achievements")}
                  </IonButton>
                </div>
              )}

            {authUser?.isAuth && (
              <div className={styles.caja_profile}>
                <IonRow>
                  <IonCol sizeLg="3" sizeXs="0"></IonCol>
                  <IonCol className={styles.cabecera}>{t("games")}</IonCol>
                  <IonCol className={styles.cabecera}>{t("correct")}</IonCol>
                  <IonCol className={styles.cabecera}>{t("incorrect")}</IonCol>
                  <IonCol sizeLg="3" sizeXs="0"></IonCol>
                </IonRow>
                <IonRow>
                  <IonCol sizeLg="3" sizeXs="0"></IonCol>
                  <IonCol
                    className={styles.item_valor + " " + styles.color_primary}
                  >
                    {authUser.user?.DatosExtendido?.NumeroPartida}
                  </IonCol>
                  <IonCol className={styles.item_valor + " " + styles.verde}>
                    {authUser.user?.DatosExtendido?.Aciertos}
                  </IonCol>
                  <IonCol className={styles.item_valor + " " + styles.error}>
                    {authUser.user?.DatosExtendido?.Errores}
                  </IonCol>
                  <IonCol sizeLg="3" sizeXs="0"></IonCol>
                </IonRow>
              </div>
            )}
            <div className={styles.caja_profile}>
              <h3>{t("game_helps")}</h3>
              <IonRow>
                <IonCol className="text_center">
                  <img
                    alt={t("life_50")}
                    src="/assets/icon/vida.png"
                    className="icon_ayuda_perfil"
                  ></img>
                </IonCol>
                <IonCol className="text_center">
                  <img
                    alt={t("help_50")}
                    src="/assets/icon/ayuda50.png"
                    className="icon_ayuda_perfil"
                  ></img>
                </IonCol>
                <IonCol className="text_center">
                  <img
                    alt={t("help_longer")}
                    src="/assets/icon/ayudaMasTiempo.png"
                    className="icon_ayuda_perfil"
                  ></img>
                </IonCol>
                <IonCol className="text_center">
                  <img
                    alt={t("help_not_to_lose_points")}
                    src="/assets/icon/ayudaRecuperarPuntos.png"
                    className="icon_ayuda_perfil"
                  ></img>
                </IonCol>
                <IonCol className="text_center">
                  <img
                    alt={t("help_correct_answer")}
                    src="/assets/icon/ayudaRespuestaCorrecta.png"
                    className="icon_ayuda_perfil"
                  ></img>
                </IonCol>
                <IonCol className="text_center">
                  <img
                    alt={t("help_doble")}
                    src="/assets/icon/oportunidadDoble.png"
                    className="icon_ayuda_perfil"
                  ></img>
                </IonCol>
              </IonRow>
              <IonRow>
                <IonCol className="text_center">
                  {authUser.user?.DatosExtendido?.Vida}
                </IonCol>
                <IonCol className="text_center">
                  {authUser.user?.DatosExtendido?.Ayuda50}
                </IonCol>
                <IonCol className="text_center">
                  {authUser.user?.DatosExtendido?.AyudaMasTiempo}
                </IonCol>
                <IonCol className="text_center">
                  {authUser.user?.DatosExtendido?.RecuperarPuntosPerdido}
                </IonCol>
                <IonCol className="text_center">
                  {authUser.user?.DatosExtendido?.AyudaPreguntaCorrecta}
                </IonCol>
                <IonCol className="text_center">
                  {authUser.user?.DatosExtendido?.AyudaContestar2Veces}
                </IonCol>
              </IonRow>

              <IonButton color="success" shape="round" onClick={tienda}>
                {t("buy_more")}
              </IonButton>
            </div>

            {authUser?.isAuth && (
              <div className={styles.caja_profile}>
                <IonRow>
                  <IonCol sizeLg="3" sizeXs="0"></IonCol>
                  <IonCol className={styles.cabecera}>
                    {" "}
                    {t("better_position")}
                  </IonCol>
                  <IonCol className={styles.cabecera}>
                    {" "}
                    {t("best_score")}
                  </IonCol>
                  <IonCol className={styles.cabecera}>
                    {t("highest_level")}
                  </IonCol>
                  <IonCol sizeLg="3" sizeXs="0"></IonCol>
                </IonRow>
                <IonRow>
                  <IonCol sizeLg="3" sizeXs="0"></IonCol>
                  <IonCol
                    className={styles.item_valor + " " + styles.color_primary}
                  >
                    {authUser.user?.DatosExtendido?.MejorPosicion === 0
                      ? "-"
                      : authUser.user?.DatosExtendido?.MejorPosicion}
                  </IonCol>
                  <IonCol
                    className={styles.item_valor + " " + styles.color_primary}
                  >
                    {authUser.user?.DatosExtendido?.MejorPuntuacion === 0
                      ? "-"
                      : authUser.user?.DatosExtendido?.MejorPuntuacion}
                  </IonCol>
                  <IonCol
                    className={styles.item_valor + " " + styles.color_primary}
                  >
                    {authUser.user?.DatosExtendido?.MejorNivel === 0
                      ? "-"
                      : authUser.user?.DatosExtendido?.MejorNivel}
                  </IonCol>
                  <IonCol sizeLg="3" sizeXs="0"></IonCol>
                </IonRow>
              </div>
            )}

            <div className="c_menu_lateral">
              {authUser?.isAuth && (
                <IonList>
                  <IonItem onClick={modalNick}>
                    <IonIcon color="primary" icon={create} />
                    <IonLabel className="btn_menu_home" color="primary">
                      {t("change_nickname")}
                    </IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>

                  <IonItem onClick={modalAvatar}>
                    <IonIcon color="primary" icon={person} />
                    <IonLabel className="btn_menu_home">
                      {" "}
                      {t("change_avatar")}
                    </IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>

                  <IonItem onClick={modalIdioma}>
                    <IonIcon color="primary" icon={language} />
                    <IonLabel className="btn_menu_home">
                      {" "}
                      {t("change_language")}
                    </IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>

                  <IonItem onClick={modalEliminarCuenta}>
                    <IonIcon color="primary" icon={trash} />
                    <IonLabel className="btn_menu_home">
                      {" "}
                      {t("delete_account")}
                    </IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={modalPrivacidad}>
                    <IonIcon color="primary" icon={fileTrayFull} />
                    <IonLabel className="btn_menu_home">
                      {" "}
                      {t("privacy_policy")}
                    </IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={modalSobreLaApp}>
                    <IonIcon color="primary" icon={gameController} />
                    <IonLabel className="btn_menu_home">
                      {" "}
                      {t("about_the_app")}
                    </IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={facebookLink}>
                    <IonIcon color="primary" icon={logoFacebook} />
                    <IonLabel className="btn_menu_home"> Facebook</IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={twitterLink}>
                    <IonIcon color="primary" icon={logoTwitter} />
                    <IonLabel className="btn_menu_home"> Twitter</IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={youtubeLink}>
                    <IonIcon color="primary" icon={logoYoutube} />
                    <IonLabel className="btn_menu_home"> Youtube</IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={instagramLink}>
                    <IonIcon color="primary" icon={logoInstagram} />
                    <IonLabel className="btn_menu_home">Instagram</IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={pekegamesLink}>
                    <IonIcon color="primary" icon={logoWebComponent} />
                    <IonLabel className="btn_menu_home"> PekeGames</IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                </IonList>
              )}
              {!authUser?.isAuth && (
                <IonList>
                  <IonItem onClick={modalIdioma}>
                    <IonIcon color="primary" icon={language} />
                    <IonLabel className="btn_menu_home">
                      {" "}
                      {t("change_language")}
                    </IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={modalPrivacidad}>
                    <IonIcon color="primary" icon={fileTrayFull} />
                    <IonLabel className="btn_menu_home">
                      {" "}
                      {t("privacy_policy")}
                    </IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={modalSobreLaApp}>
                    <IonIcon color="primary" icon={gameController} />
                    <IonLabel className="btn_menu_home">
                      {" "}
                      {t("about_the_app")}
                    </IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={facebookLink}>
                    <IonIcon color="primary" icon={logoFacebook} />
                    <IonLabel className="btn_menu_home"> Facebook</IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={twitterLink}>
                    <IonIcon color="primary" icon={logoTwitter} />
                    <IonLabel className="btn_menu_home"> Twitter</IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={youtubeLink}>
                    <IonIcon color="primary" icon={logoYoutube} />
                    <IonLabel className="btn_menu_home"> Youtube</IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                  <IonItem onClick={pekegamesLink}>
                    <IonIcon color="primary" icon={logoWebComponent} />
                    <IonLabel className="btn_menu_home"> PekeGames</IonLabel>
                    <IonIcon icon={chevronForward} />
                  </IonItem>
                </IonList>
              )}
            </div>

            {authUser?.isAuth && (
              <div className={styles.caja_logout}>
                <IonButton color="danger" onClick={logout}>
                  {t("logout")}
                </IonButton>
              </div>
            )}

            <div className="margin_buttom_banner">
              <p className={styles.version}>{version}</p>
            </div>
          </IonCol>
          <IonCol sizeMd="4"></IonCol>
        </IonRow>

        <IonModal
          isOpen={shownickModal}
          onDidDismiss={() => setShownickModal(false)}
          cssClass={styles.mini}
        >
          <IonHeader>
            <IonToolbar>
              <IonButtons slot="end">
                <IonButton onClick={() => setShownickModal(false)}>
                  {" "}
                  {t("close")}
                </IonButton>
              </IonButtons>
              <IonTitle>{t("change_nickname")}</IonTitle>
            </IonToolbar>
          </IonHeader>
          <IonContent>
            <img
              alt={t("logo_peke")}
              className={styles.login_logo}
              src="/assets/icon/logo_pk.png"
            ></img>
            <p className="titulo_modal"> {t("change_nickname")}</p>
            <IonItem>
              <IonLabel color="secondary" position="floating">
                {t("change_nickname")}
              </IonLabel>
              <IonInput
                value={nick}
                onIonChange={(e) => setNick(e.detail.value!)}
                clearInput
              ></IonInput>
            </IonItem>

            <div className={styles.caja_boton}>
              <IonButton
                shape="round"
                className="btn_sty"
                color="primary"
                onClick={modificarNick}
              >
                {t("save")}
              </IonButton>
            </div>
          </IonContent>
        </IonModal>

        <IonModal
          isOpen={showAvatarModal}
          onDidDismiss={() => setShowAvatarModal(false)}
          cssClass={styles.mini}
        >
          <IonHeader>
            <IonToolbar>
              <IonButtons slot="end">
                <IonButton onClick={() => setShowAvatarModal(false)}>
                  {" "}
                  {t("close")}
                </IonButton>
              </IonButtons>
              <IonTitle>{t("change_avatar")}</IonTitle>
            </IonToolbar>
          </IonHeader>
          <IonContent>
            <img
              alt={t("logo_peke")}
              className={styles.login_logo}
              src="/assets/icon/logo_pk.png"
            ></img>
            <p className="titulo_modal"> {t("change_avatar")}</p>
            <IonItem>
              <div className="text_center contenido_scroll">{final}</div>
            </IonItem>
          </IonContent>
        </IonModal>

        <IonModal
          isOpen={showIdiomaModal}
          onDidDismiss={() => setShowIdiomaModal(false)}
          cssClass={styles.mini}
        >
          <IonHeader>
            <IonToolbar>
              <IonButtons slot="end">
                <IonButton onClick={() => setShowIdiomaModal(false)}>
                  {" "}
                  {t("close")}
                </IonButton>
              </IonButtons>
              <IonTitle>{t("change_language")}</IonTitle>
            </IonToolbar>
          </IonHeader>
          <IonContent>
            <img
              alt={t("logo_peke")}
              className={styles.login_logo}
              src="/assets/icon/logo_pk.png"
            ></img>
            <p className="titulo_modal"> {t("change_language")}</p>
            <div className="c_menu_lateral">
              <IonList>
                <IonItem onClick={() => modificarIdioma("en")}>
                  <IonIcon color="primary" icon={language} />
                  <IonLabel className="btn_menu_home" color="primary">
                    {t("lang_en")}
                  </IonLabel>
                  <IonIcon icon={chevronForward} />
                </IonItem>
                <IonItem onClick={() => modificarIdioma("es")}>
                  <IonIcon color="primary" icon={language} />
                  <IonLabel className="btn_menu_home" color="primary">
                    {t("lang_es")}
                  </IonLabel>
                  <IonIcon icon={chevronForward} />
                </IonItem>
                <IonItem onClick={() => modificarIdioma("pt")}>
                  <IonIcon color="primary" icon={language} />
                  <IonLabel className="btn_menu_home" color="primary">
                    {t("lang_pt")}
                  </IonLabel>
                  <IonIcon icon={chevronForward} />
                </IonItem>
                <IonItem onClick={() => modificarIdioma("fr")}>
                  <IonIcon color="primary" icon={language} />
                  <IonLabel className="btn_menu_home" color="primary">
                    {t("lang_fr")}
                  </IonLabel>
                  <IonIcon icon={chevronForward} />
                </IonItem>
              </IonList>
            </div>
          </IonContent>
        </IonModal>

        <IonModal
          isOpen={showPrivacidadModal}
          onDidDismiss={() => setShowPrivacidadModal(false)}
          cssClass="fullscreen"
        >
          <IonHeader>
            <IonToolbar>
              <IonButtons slot="end">
                <IonButton onClick={() => setShowPrivacidadModal(false)}>
                  {" "}
                  {t("close")}
                </IonButton>
              </IonButtons>
              <IonTitle> {t("privacy_policy")}</IonTitle>
            </IonToolbar>
          </IonHeader>
          <IonContent>
            <iframe
              id="mymodal"
              src="https://www.pekegames.app/home/privacy"
              title={t("privacy_policy")}
              className="iframeStyle"
            ></iframe>
          </IonContent>
        </IonModal>

        <CrearCuenta
          estadoModal={loginModal}
          eventoCerrar={cerrarModal}
          navegar={false}
        ></CrearCuenta>

        <IonModal
          isOpen={showCuentaModal}
          onDidDismiss={() => setShowCuentaModal(false)}
          cssClass={styles.mini}
        >
          <IonHeader>
            <IonToolbar>
              <IonButtons slot="end">
                <IonButton onClick={() => setShowCuentaModal(false)}>
                  {" "}
                  {t("close")}
                </IonButton>
              </IonButtons>
              <IonTitle>{t("delete_account")}</IonTitle>
            </IonToolbar>
          </IonHeader>
          <IonContent>
            <div className="contenido_scroll">
              <img
                alt={t("logo_peke")}
                className={styles.login_logo}
                src="/assets/icon/logo_pk.png"
              ></img>
              <p className="titulo_modal"> {t("delete_account")}</p>
              <p className={styles.caja_profile}>
                {" "}
                {t("delete_account_descripcion")}
              </p>
              <div className={styles.caja_logout}>
                <IonButton color="danger" onClick={deleteCuentaPeke}>
                  {t("delete_account")}
                </IonButton>
              </div>
            </div>
          </IonContent>
        </IonModal>

        <ListadoLogro
          estadoModal={logroModal}
          eventoCerrar={cerrarModalLogro}
        ></ListadoLogro>

        <VerLogro
          estadoModal={verLogroModal}
          logro={logroSelect}
          eventoCerrar={cerrarModalVerLogro}
        >
          {" "}
        </VerLogro>

        <IonModal
          isOpen={showAppModal}
          onDidDismiss={() => setShowAppModal(false)}
          cssClass={styles.mini}
        >
          <IonHeader>
            <IonToolbar>
              <IonButtons slot="end">
                <IonButton onClick={() => setShowAppModal(false)}>
                  {" "}
                  {t("close")}
                </IonButton>
              </IonButtons>
              <IonTitle>{t("about_the_app")}</IonTitle>
            </IonToolbar>
          </IonHeader>
          <IonContent>
            <img
              alt={t("logo_peke")}
              className={styles.login_logo}
              src="/assets/icon/logo_pk.png"
            ></img>
            <p className="titulo_modal"> {t("about_the_app")}</p>
            <p className={styles.caja_profile}>
              {" "}
              {t("about_the_app_descripcion")}
            </p>
          </IonContent>
        </IonModal>

        {logroGanaModal && authUser !== null && authUser.isAuth ? (
          <GanaLogro
            estadoModal={logroGanaModal}
            eventoCerrar={cerrarModaGanalLogro}
            logroId={1}
          ></GanaLogro>
        ) : (
          ""
        )}
      </IonContent>
    </IonPage>
  );
};

export default Profile;
