export const RandomIntFromInterval = (min: number, max: number) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
};

export const shareHander = async (t) => {
  let titleApp = t("name_app");
  titleApp += " : " + t("share_title");
  await Share.share({
    title: titleApp,
    text: t("description_app"),
    url: configData.web,
    dialogTitle: t("share_title"),
  });
};
export function MenuApp(present, t) {
  return (
    <IonButtons slot="primary">
      <IonButton
        color="secondary"
        onClick={() =>
          present({
            buttons: [
              {
                text: t("tourist_points"),
                icon: images,
                handler: () => history.push("photo"),
              },
              {
                text: t("cities"),
                icon: map,
                handler: () => history.push("cities"),
              },
              {
                text: t("home"),
                icon: homeSharp,
                handler: () => history.push("dashboard"),
              },

              {
                text: t("other_apps"),
                icon: apps,
                handler: () => history.push("apps"),
              },
              {
                text: t("tutorial"),
                icon: alertCircleSharp,
                handler: () => history.push("ayuda"),
              },
              {
                text: t("share"),
                icon: shareSocialSharp,
                handler: () => shareHander(t),
              },
              {
                text: t("store"),
                icon: storefrontSharp,
                handler: () => history.push("store"),
              },
              {
                text: t("settings"),
                icon: settings,
                handler: () => history.push("profile"),
              },
              
              {
                text: t("rate_app"),
                icon: starHalf,
                //handler: () => RateApp.requestReview(),
              }
            ],
            header: t("app_options"),
          })
        }
      >
        <IonIcon
          slot="icon-only"
          ios={ellipsisHorizontal}
          md={ellipsisVertical}
        />
      </IonButton>
    </IonButtons>
  );
}

export function deshabilitarBoton(
  key: number,
  respuesta1: boolean,
  respuesta2: boolean,
  respuesta3: boolean,
  respuesta4: boolean
): boolean {
  switch (key) {
    case 0:
      return respuesta1;
    case 1:
      return respuesta2;
    case 2:
      return respuesta3;
    case 3:
      return respuesta4;
    default:
      return false;
  }
}

